.user {
  text-align: center;
}

.photo-block {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  border-radius: 50%;
  overflow: hidden;
}

.photo {
  position: absolute;
  left: -30px;
  top: 0;
  width: 200%;
  margin-right: 50px;
}

.mount {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 100px;
}
